import React,{useEffect} from 'react'
import {useSelector,useDispatch} from 'react-redux';
import { Link, useNavigate} from 'react-router-dom'
import logo from "../logo.png"
import "./Navbar.css"
import Avatar from './Avatar'
import { setcurrentUser } from '../actions/Currentuser';
const Navbar = () => {
  const dispatch = useDispatch();
  const user = useSelector((state)=>(state.currentUserreducer));
  const navigate = useNavigate();
  useEffect(()=>{
    dispatch(setcurrentUser(JSON.parse(localStorage.getItem('profile'))))
  },[dispatch])

  const handlelogout = () =>{
    dispatch({type:"LOGOUT"})
    navigate("/auth")
    dispatch(setcurrentUser(null))
  }

  return (
    <>
    <div className='navbar'>
        <Link to= '/' className='nav'>
            <img className='img' src={logo}/>
        </Link>
        <div className='a'>
        <ul>
            <li>
                <button href="" className='nav-item'>about</button>
                <button href="" className='nav-item'>product</button>
                <button href="" className='nav-item'>for team</button>
            </li>
        </ul>
        </div>
        <input className='b' type="text" placeholder='search'/>
        {user===null?
        <Link to="/Auth" className='btn'>Login</Link>:<>
        <Avatar backgroundColor="rgb(84, 84, 244)" py="8px" px="7px" borderRadius="60%" color="white" fontSize="20px" cursor="pointer" height="20px" width="20px" ><Link style={{"textDecoration":"none","marginLeft":"2px"}} to={`/users/${user?.result?._id}`}>{user.result.name.charAt(0).toUpperCase()}</Link></Avatar>
        <button className='btn' onClick={()=>{handlelogout()}}>Logout</button>
        </>}
    </div>
    </>
  )
}

export default Navbar
